/**@dateTimeFliter 
 * @time[] 						Date  		
 * @part[,:0]				Number        :0: 1:  2:
 * @dateComplete[,:true] 	Boolean 	0:true:,false:
 * @timeComplete[,:true] 	Boolean 	0:true:,false:
 * @dateConnector[,:-] 		String 		  : - : /
 * @timeConnector[,::] 		String 		   : - : /
 * @hour12[,:false]          Boolean     12   : true:12   false:24
 * @return   '2019-11-25 15:05:54'  String    
 * **/
commen.dateTimeFliter = function(time, part = 0, dateComplete = true, timeComplete = true, dateConnector = '-',
	timeConnector = ':', hour12 = false) {
	let year = time.getFullYear()
	let month = time.getMonth() + 1
	let day = time.getDate()
	let hour = time.getHours()
	let minute = time.getMinutes()
	let second = time.getSeconds()
	let dateStr = ''
	let timeStr = ''
	//
	if (dateComplete) { //0
		if (month < 10) {
			month = '0' + month
		}
		if (day < 10) {
			day = '0' + day
		}
	}
	dateStr = year + dateConnector + month + dateConnector + day
	//
	//
	if (hour12) {
		if (hour > 12) {
			hour = hour - 12
			if (timeComplete) {
				if (hour < 10) {
					hour = ' ' + '0' + hour
				} else {
					hour = ' ' + hour
				}
			}
		} else {
			if (timeComplete) {
				if (hour < 10) {
					hour = ' ' + '0' + hour
				} else {
					hour = ' ' + hour
				}
			}
		}
	}
	//
	if (timeComplete) { //0
		if (minute < 10) {
			minute = '0' + minute
		}
		if (second < 10) {
			second = '0' + second
		}
	}
	// timeStr = hour + timeConnector + minute + timeConnector + second
	timeStr = hour + timeConnector + minute 
	//
	if (part == 0) {
		return dateStr + ' ' + timeStr
	} else if (part == 1) {
		return dateStr
	} else if (part == 2) {
		return timeStr
	}
	return ''
}



export default commen
